.logo {
  background-image: url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/2b326fe971cd296bd54667a9b4a63c4b0f19fab3/week5/logo-ANSWER.svg");
  background-repeat: no-repeat;
  width: 151px;
  height: 40px;
  text-indent: 101%;
  white-space: nowrap;
  overflow: hidden; }

.menu a {
  position: relative; }

.menu__item a.active, .menu__item a:hover {
  color: #198754; }
  .menu__item a.active::before, .menu__item a:hover::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 4px;
    height: 100%;
    background-color: #198754; }

.object-fit-cover {
  object-fit: cover; }

.reply {
  position: relative; }
  .reply__content {
    position: absolute;
    transition: 0.5s;
    width: 0;
    height: 0;
    bottom: 0;
    right: 0;
    overflow: hidden; }
    .reply__content textarea {
      border: 0;
      outline: 0;
      padding: 1rem;
      width: 100%;
      height: 100%; }
    .reply__content.active {
      width: 100%;
      height: 100%; }

.checkbox {
  display: none; }
  .checkbox__label {
    cursor: pointer;
    position: relative;
    padding-left: 0.75rem; }
    .checkbox__label::before {
      position: absolute;
      content: "\2714";
      top: 0;
      left: -24px;
      width: 20px;
      height: 20px;
      font-size: 14px;
      padding: 0px 0px 15px 1px;
      border: 2px solid #000;
      border-radius: 10%;
      display: inline-block;
      color: transparent; }
  .checkbox:checked + .checkbox__label::before {
    background-color: #198754;
    color: white; }

.viewInfo__img {
  width: 141px;
  height: 141px;
  border-radius: 2px; }

.viewInfo__btns {
  height: 0;
  transition: 0.5s; }
  .viewInfo__btns input {
    display: none; }
  .viewInfo__btns.active {
    margin-top: 2rem;
    display: flex;
    height: 35px; }
    .viewInfo__btns.active input {
      display: block; }

.form-control:disabled, .form-control:read-only {
  background-color: #F8F9FA; }
